.sticker-set-title {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
}

.sticker-set-title span {
    display: block;
    width: 100%;
    padding: 6px;
    box-sizing: border-box;
}

.sticker-set-content {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.sticker-set-item {
    background: transparent;
}

.sticker-set-item-sticker {
    pointer-events: none;
}

