.stickers-picker {
    width: 338px;
    height: 358px;
    background: transparent;
}

.stickers-picker-scroll {
    background: transparent;
    margin-right: -15px;
    padding: 0 12px 6px 6px;
    height: 356px;
    overflow-y: scroll;
}

